/**
 * 
 * @param areaCode 
 * @param content 
 * @returns {string}
 */
export function getContactWithCode(areaCode, content) {
    let result;
    if (!areaCode || areaCode === '86') {
        result = content;
    } else {
        result = `${areaCode} ${content}`;
    }
    return result;
}

/**
 * 
 * @param contact 
 * @returns {{contactContent: *, contactCode: (*|string)}}
 */
export function getContactCodeAndContent(contact = '') {
    if (!contact) {
        return {
            contactCode: '',
            contactContent: '',
        };
    }
    const phoneRegLoose = /^(?:\+?(86|852|886)\s?)?(.*)$/;
    const contactCode = contact.match(phoneRegLoose)[1] || '86';
    const contactContent = contact.match(phoneRegLoose)[2];
    return {
        contactCode,
        contactContent,
    };
}

/**
 * 
 * @param {*} contact
 */
export function getAreaCodeObj(contact = '') {
    const codeList = [
        { label: '', areaAbbr: 'ZH', code: '+86', value: '' },
        { label: '', areaAbbr: 'HK', code: '+852', value: '852' },
        { label: '', areaAbbr: 'TW', code: '+886', value: '886' },
    ];
    const phoneRegLoose = /^(?:\+?(86|852|886)\s?)?(.*)$/;
    const contactCode = contact.match(phoneRegLoose)[1] || '86';
    if (contactCode === '' || contactCode === '86') {
        return { label: '', areaAbbr: 'ZH', code: '+86', value: '' };
    }
    return codeList.find(item => item.value === contactCode);
}

/**
 * 
 */
export function getDeviceType() {
    let type = ''; // 

    const UA = navigator.userAgent;
    let isAndroid = UA.indexOf('Android') > -1 || UA.indexOf('Adr') > -1; // android
    let isiOS = !!UA.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); // ios

    if (isAndroid) {
        type = 'android';
    } else if (isiOS) {
        type = 'ios';
    }

    return type;
}
